#dialog {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgba(10, 10, 10, .7);
  transition: all .4s;
  z-index: 100000;
}

#dialog.opened {
  visibility: visible;
  opacity: 1;
}

.dialog-content {
  border-radius: 4px;
  position: relative;
  width: 500px;
  max-width: 90%;
  background: #111;
  padding: 1em 2em;
  color: #f1f1f1;
}

.dialog-footer {
  text-align: right;
}

#dialog button {
  background-color: black;
  border: 2px solid white;
  border-radius: 4px;
  padding: 8px 16px;
  color: white;
}